import classes from './News.module.css'
import NewsItem from './NewsItem/NewsItem';
const News=(props)=>{
    let newsElemets = props.newsPage.news
        .map(n => <NewsItem message={n.message} img={n.img} likeCount={n.likeCount}/>);
    return (
        <div className={classes.content}>
            <div className={classes.header}>News</div>
            <div className={classes.newsPosts}>
                {newsElemets}
            </div>
        </div>
    );
}

export default News;